<template>
  <div>
    <!-- 導航欄 -->
    <van-nav-bar
      title="商品搜索"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <!-- 搜索框 -->
    <div class="searchBox">
      <label class="searchInputLabel">
        <div class="searchIcon"><van-icon name="search" /></div>
        <input class="searchInput" type="text" ref="searchInput" v-model.trim="search" placeholder="請輸入搜索關鍵詞">
      </label>
      <div class="goSearch" @click="goSearch(search)">搜索</div>
    </div>
    <!-- 搜索歷史 -->
    <div  v-if="history.length > 0">
      <div class="historyTitle">
        <p>搜索歷史</p>
        <van-icon @click="clear" name="delete-o" />
      </div>
      <div class="historyItemsBox">
        <div class="items" @click="search = item" v-for="item in history" :key="item">
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHistoryList, setHistoryList } from '@/utils/storage'

export default {
  name: 'searchIndex',
  data () {
    return {
      search: '',
      history: getHistoryList()
    }
  },
  mounted () {
    this.$refs.searchInput.focus()
  },
  methods: {
    goSearch (key) {
      if (key) {
        this.history = this.history.filter(i => i !== key)
        this.history.unshift(key)
        setHistoryList(this.history)
        this.$router.push(`/searchlist?search=${key}`)
      }
    },
    clear () {
      this.history = []
      setHistoryList([])
    }
  }
}
</script>

<style lang="less" scoped>
  .searchBox{
    display: flex;
    justify-content: center;
    width: calc(100vw - 20px);
    margin: 10px;
    .searchInputLabel{
      display: flex;
      width: 80%;
      .searchIcon{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 10%;
        padding: 0 5px 0 10px;
        background-color: #f8f8f8ec;
      }
      .searchInput{
        width: 90%;
        padding: 5px;
        border: 0px;
        background-color: #f8f8f8ec;
      }
    }
    .goSearch{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 25%;
      background-color: rgb(255, 0, 0);
      color: #ffffff;
      border-radius: 0 5px 5px 0;
    }
  }
  .historyTitle{
    display: flex;
    justify-content: space-between;
    margin: 0 10px 5px 10px;
  }
  .historyItemsBox{
    display: flex;
    margin: 0 5px;
    flex-wrap: wrap;
    width: calc(100% - 10px);
    .items{
      width: calc(33.33% - 10px);
      margin: 5px;
      padding: 5px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #000;
      border-radius: 3px;
    }
  }
</style>
